.nav__list {
  gap: 0 20px;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  transition-duration: 2s;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  max-width: 40px;
  max-height: 40px;
}

.is-active {
  color: #00acdc;
  display: flex;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars:last-child {
  margin-right: 0;
}

.stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.cards-container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards-container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 758px) {
  .cards-container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards-container {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-inline: auto;
}

.card__text-main {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition-duration: .3s;
}

.card__text-code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__raiting {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__views {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__text-main {
  color: #34568b;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
}

